<template>
	<view class="drop-item">
		<!-- 多选渲染 -->
		<block v-if="type=='filter'">
			<scroll-view class="menu-box" :scroll-y="true">
				<view class="box" v-for="pItem in newItem[childName]" :key="pItem.key">
					<view class="title">{{pItem[fileds]}}</view>
					<view class="labels">
						<view v-for="cItem in pItem[childName]" :key="cItem.key"
							@click="selectFilterLabel(cItem,cItem.key)"
							:class="{'on':filter.findIndex(el=>el==cItem.key)!==-1}">
							{{cItem[fileds]}}
						</view>
					</view>
				</view>
			</scroll-view>
		</block>

		<!-- 单选渲染 -->
		<block v-else-if="type=='radio'">
			<scroll-view class="menu-box" :scroll-y="true">
				<view class="box" v-for="pItem in newItem[childName]" :key="pItem.key">
					<view class="title">{{pItem[fileds]}}</view>
					<view class="labels">
						<view v-for="cItem in pItem[childName]" :key="cItem.key"
							@tap="selectRadioLabel(cItem,pItem.key)"
							:class="{'on':radio.findIndex(el=>el.key==cItem.key)!==-1}">{{cItem[fileds]}}
						</view>
					</view>
				</view>
			</scroll-view>

		</block>
	</view>
</template>

<script>
	import {
		guid
	} from '../../lib/guid.js'
	export default {
		/**
		 * @property {Object}						item			当前项
		 * @property {Array}						fileds  		显示的字段（默认 ["name", "label"]）
		 * @property {String}						childName  		子菜单字段（默认 submenu）
		 * @property {String}						type  			类型	（默认 filter，可选项 radio）
		 * @property {Function() <array> {}}		change  		返回选中的当前对象，返回值为数组
		 * */

		props: {
			item: {
				type: Object,
				default () {
					return {}
				}
			},
			fileds: {
				type: String,
				default: "name"
			},
			childName: {
				type: String,
				default: 'submenu'
			},
			type: {
				type: String,
				default: 'filter'
			}
		},
		created() {
			this.init()
		},
		data() {
			return {
				filter: [],
				radio: [],
				newItem: [],

				// 多选选中数组
				selectFilterArr: [],

				// 单选选中数组
				selectRadioArr: []
			}
		},
		methods: {
			init() {
				this.newItem = JSON.parse(JSON.stringify(this.item))
				this.newItem.key = guid()
				this.setKey(this.newItem)
			},
			setKey(item) {
				if (item[this.childName]) {
					if (item[this.childName].length) {
						item[this.childName].forEach(el => {
							el.key = guid()
							this.setKey(el)
						})
					}
				} else {
					item[this.childName] = []
				}

			},

			// 多选
			selectFilterLabel(item, key) {
				// 选中之前判断是否已经选中，已选中 ? 移除选中 : 添加选中
				const sIdx = this.selectFilterArr.findIndex(el => el.key == key)
				if (sIdx >= 0) this.selectFilterArr.splice(sIdx, 1)
				else this.selectFilterArr.push(item)

				// 选中之前判断是否已经选中，已选中 ? 移除选中的 key : 添加选中的 key
				const fIdx = this.filter.findIndex(el => el == key)
				if (sIdx >= 0) this.filter.splice(fIdx, 1)
				else this.filter.push(key)

				this.$emit('change', this.selectFilterArr)
			},

			// 单选
			selectRadioLabel(item, key) {
				// 选中之前判断是否已经选中已选中 ? 移除选中 : 添加选中
				const sIdx = this.selectRadioArr.findIndex(el => el.pKey == key)
				if (sIdx >= 0 && this.selectRadioArr[sIdx].key === item.key) this.selectRadioArr.splice(sIdx, 1)
				else if (sIdx >= 0 && this.selectRadioArr[sIdx].key !== item.key) this.selectRadioArr.splice(sIdx, 1, {
					pKey: key,
					...item
				})
				else this.selectRadioArr.push({
					pKey: key,
					...item
				})

				// 选中之前判断是否已经选中，已选中 ? radio = -1 : radio = key
				const fIdx = this.radio.findIndex(el => el.pKey == key)
				if (fIdx >= 0 && this.radio[fIdx].key === item.key) this.radio.splice(fIdx, 1)
				else if (fIdx >= 0 && this.radio[fIdx].key !== item.key) this.radio.splice(fIdx, 1, {
					pKey: key,
					key: item.key
				})
				else this.radio.push({
					pKey: key,
					key: item.key
				})
				this.$emit('change', this.selectRadioArr)
			},

			// 移除 guid
			removeGuid(filed) {
				// 由于 key 是组件渲染是手动生成的，不需要反馈回去，所以需要移除
				const arr = JSON.parse(JSON.stringify(this[filed]))
				arr.forEach(el => {
					delete el.key
					el[this.childName] && el[this.childName].forEach(ele => {
						delete ele.key
					})
				})
				return arr
			}
		}
	}
</script>

<style lang="scss" scoped>
	view {
		display: flex;
		flex-wrap: nowrap;
	}

	.drop-item {
		width: 100%;
		align-items: center;
		flex-direction: column;

		.title {
			width: 100%;
			font-size: 26upx;
			color: #888;
		}

		.labels {
			flex-direction: row;
			flex-wrap: wrap;

			.on {
				border-color: $uni-color-primary;
				background-color: $uni-color-primary;
				color: #fff;
			}

			>view {
				width: calc((698upx - 30upx * 3) / 4 - 1upx * 2);
				height: 60upx;
				border: solid 1rpx #adadad;
				border-radius: 4upx;
				margin-right: 20upx;
				margin-top: 16upx;
				font-size: 24upx;
				flex-direction: row;
				justify-content: center;
				align-items: center;
			}
		}

		.menu-box {
			width: 698rpx;
			height: calc(690upx - 120upx);
			flex-shrink: 1;

			.box {
				width: 100%;
				padding-top: 32upx;
				flex-direction: column;
			}
		}
	}
</style>
